"use strict";

/**
 * JS
 */
function loadScript(url) {
  return new Promise((resolve, reject) => {
    if (window.TencentCaptcha === undefined) {
      let script = document.createElement("script");
      let head = document.getElementsByTagName("head")[0];
      script.type = "text/javascript";
      script.charset = "UTF-8";
      script.src = url;
      head.appendChild(script);
      // 
      script.onload = function () {
        resolve();
      };
      // 
      script.onerror = function () {
        reject(new Error(""));
      };
    } else {
      resolve();
    }
  });
}

/**
 * creater
 * @param {function} callback ,  https://cloud.tencent.com/document/product/1110/36841
 * @param {object} options ,  https://cloud.tencent.com/document/product/1110/36841#pzcs
 * @returns {Promise<object>} TencentCaptcha show()  destroy()  getTicket() ticket
 */
async function captchaCreater(callback, options) {
  try {
    // 
    await loadScript("https://ssl.captcha.qq.com/TCaptcha.js");
    // AppId
    const { result } = await uniCloud.callFunction({
      name: "tencentcloud-plugin",
      data: {
        module: "CAPTCHA",
        action: "getAppId",
      },
    });
    return new TencentCaptcha(`${result}`, callback, options);
  } catch (error) {
    throw new Error(error);
  }
}

/**
 * 
 * @param {*} params
 * @param {integer} params.captchaType - 9
 * @param {string} params.ticket - 
 * @param {string} params.randstr - 
 * @param {integer} [params.businessId] -  ID ID 
 * @param {integer} [params.sceneId] -  ID ID 
 * @param {string} [params.macAddress] - mac 
 * @param {string} [params.imei] - 
 * @param {integer} [params.needGetCaptchaTime] - 1
 */
async function describeCaptchaResult({
  captchaType,
  ticket,
  randstr,
  businessId,
  sceneId,
  macAddress,
  imei,
  needGetCaptchaTime,
}) {
  try {
    if (!captchaType || !ticket || !randstr) {
      throw new Error("");
    }
    const result = await uniCloud.callFunction({
      name: "tencentcloud-plugin",
      data: {
        module: "CAPTCHA",
        action: "describeCaptchaResult",
        CaptchaType: captchaType,
        Ticket: ticket,
        Randstr: randstr,
        BusinessId: businessId,
        SceneId: sceneId,
        MacAddress: macAddress,
        Imei: imei,
        NeedGetCaptchaTime: needGetCaptchaTime,
      },
    });
    return result;
  } catch (error) {
    throw new Error(error);
  }
}

export { captchaCreater, describeCaptchaResult };
